#anno_paper {
    position: fixed;
    width:100%;
    height: 100%;
    z-index: 0;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}
    #paper_container {
        /* position: fixed; */
        display: flex;
        width: 90%;
        height: 90%;
        background-color: rgba(219, 188, 175, 0.97);
        border-radius: 8px;
        flex-direction: column;
        align-items: center;
    }
        #paper_container_row1 {
            display: flex;
            width: 100%;
            height: 10%;
            margin: 5px;
            /* background-color: yellow; */
            justify-content: left;
            align-items: center;
            /* align-content: space-around; */
            font-size: large;
            font-family: 'Comfortaa', cursive;
        }
            #paper_container_row1_title {
                width: 60%;
                height: 100%;
                padding-left: 30px;
                /* background-color: forestgreen; */
            }
            #paper_container_row1_trope_tooltip {
                text-align: left;
                width: 400px;
                height: 200px;
                top: 0;
                left: calc(50% - 200px - 8px);
                background-color: #555;
                color: #fff;
                border-radius: 6px;
                padding: 16px;
                z-index: 2;
                margin: 8px 5px;
                position: absolute;
                opacity: 0;
                transition: opacity 0.3s;
                /* visibility: hidden; */
                /* visibility: hidden;
                left: 50%;
                margin-left: -60px;
                
                 */
            }
                #paper_container_row1_trope_tooltip_title {
                    font-size: x-large;
                    font-weight: 700;
                    margin: 0;
                    margin-bottom: 5px;
                }
                #paper_container_row1_trope_tooltip_subtitle {
                    font-size: large;
                    font-weight: 500;
                    margin: 0;
                    margin-bottom: 15px;
                }
                #paper_container_row1_trope_tooltip_content {
                    /* font-size: ; */
                    margin: 0;
                }
        #paper_container_row2 {
            display: flex;
            width: 100%;
            height: 90%;
            margin-top: 10px;
            /* background-color: red; */
            justify-content: center;
            align-items: center;
        }
            #paper_container_row2_corpus_content {
                display: flex;
                width: 80%;
                height: 90%;
                margin-left:5px;
                margin-right: 15px;
                flex-direction: column;
                /* background-color: blue; */
                overflow-x: hidden;
                overflow-y: scroll;
            }
                .paper_container_row2_corpus_sentence_box {
                    display: flex;
                    width: 100%;
                    /* height: auto; */
                    margin: 5px;
                    background-color: white;
                    
                }
                    .paper_container_row2_corpus_sentence_index {
                        display: flex;
                        width: 4%;
                        height: auto;
                        background-color: wheat;
                        justify-content: center;
                        align-items: center;
                        font-size: small;
                        font-family: 'Comfortaa', cursive;
                    }
                    .paper_container_row2_corpus_sentence {
                        display: flex;
                        width: 90%;
                        height: auto;
                        padding: 8px;
                        /* background-color: violet; */
                    }
            #paper_container_row2_trope_group {
                display: flex;
                width: 20%;
                height: 90%;
                margin-right: 5px;
                flex-direction: column;
                border: 3px solid rgb(245, 122, 122);
                border-radius: 8px;
                padding: 4px;
                /* background-color: green; */
                /* -webkit-box-pack: center;
                justify-content: center;
                -webkit-box-align: center;
                align-items: center; */
                align-items: center;
                overflow-x: hidden;
                overflow-y: scroll;
            }
                .paper_container_row2_trope_box {
                    display: flex;
                    flex-direction: column;
                    width: 95%;
                    height: auto;
                    margin: 5px;
                    /* background-color: orange; */
                    
                }
                    .paper_container_row2_trope_title {
                        padding: 4px;
                        background-color: rgb(224, 224, 224);
                        display: flex;
                        justify-content: center;
                        align-items: center;
                    }
                    .paper_container_row2_trope_title:hover {
                        padding: 4px;
                        background-color: lightcyan;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        cursor: help;
                    }
                    .paper_container_row2_trope_option_group {
                        display: flex;
                        align-content: space-around;
                    }
                        .paper_container_row2_trope_option {
                            display: flex;
                            width:80px;
                            height: 20px;
                            padding: 3px;
                            justify-content: center;
                            align-items: center;
                        }
                        /* .paper_container_row2_trope_option_selected {
                            display: flex;
                            width:80px;
                            height: 20px;
                            padding: 3px;
                            -webkit-box-pack: center;
                            justify-content: center;
                            -webkit-box-align: center;
                            align-items: center;
                            background-color: green;
                        } */
                        .paper_container_row2_trope_option:hover {
                            display: flex;
                            width:80px;
                            height: 20px;
                            padding: 3px;
                            justify-content: center;
                            align-items: center;
                            background-color: rgb(151, 151, 126);
                            cursor: pointer;
                        }
                    .paper_container_row2_trope_evidence_group {
                        display: flex;
                        width: 100%;
                        height: auto;
                        margin: 8px 0px;
                        flex-wrap: wrap;
                    }
                        .paper_container_row2_trope_evidence_input_wrapper {
                            width: 40px;
                            height: 15px;
                            margin-left: 4px;
                            margin-right: 8px;
                        }
                        .paper_container_row2_trope_evidence_input {
                            width: 35px;
                            height: 15px;
                            padding: 0px 4px;
                        }
                        .paper_container_row2_trope_evidence {
                            width: 20px;
                            height: 18px;
                            margin: 2px 4px;
                            /* padding: 0px 2px; */
                            background-color: grey;
                            text-align: center;
                            vertical-align: middle;
                            line-height: 18px;
                        }
                        .paper_container_row2_trope_evidence:hover {
                            width: 20px;
                            height: 18px;
                            margin: 2px 4px;
                            /* padding: 0px 2px; */
                            background-color: lightgrey;
                            text-align: center;
                            vertical-align: middle;
                            line-height: 18px;
                            cursor: pointer;
                        }
                    .paper_container_row2_trope_box_line {
                        width: 90%;
                        height: 1px;
                        border-bottom: 1px dashed rgba(0, 0, 0, 0.25);
                    }
#corpus_list {
    width: 50%;
    height: 600px;
    justify-content: center;
    align-items: center;
    margin: 5% auto;
    display: flex;
    flex-direction: column;
}
    #corpus_list_title {
        width: auto;
        height: 40px;
        font-size: xx-large;
        text-align: center;
        font-family: 'Permanent Marker', cursive;
        color: red;
        margin-bottom: 25px;
    }
    #corpus_list_content {
        width: auto;
        height: 500px;
        overflow-x: hidden;
        overflow-y: scroll;
        border: 1px solid tomato;
        padding: 0px 10px;
        display: flex;
        flex-direction: column;
    }
    #corpus_list_content::-webkit-scrollbar{
        display:none;
    }
        .corpus_list_item {
            background-color: rgba(250, 250, 210, 1.0);
            width: auto;
            height:50px;
            text-align: left;
            border-radius: 5px;
            margin: 10px 0px;
            display: flex;
            justify-content: left;
            align-items: center;
        }
        .corpus_list_item:hover {
            background-color: rgb(245, 245, 171);
            cursor: pointer;
            width: auto;
            height: 54px;
            text-align: left;
            margin: 10px 0px;
            font-size: large;
            display: flex;
            justify-content: left;
            align-items: center;
        }
        .corpus_list_item span{
            display: inline-block;
            vertical-align: middle;
            font-family: 'Comfortaa', cursive;
            padding: 0px 20px;
        }
        .corpus_list_item_title {
            width: 80%;
        }
        .corpus_list_item_status {
            text-align: right;
            width: 20%;
        }
    #btn_group {
        display: flex;
        width: 100%;
        height: 60px;
        justify-content: center;
        align-items: center;
    }
        #load_btn {
            margin-top: 20px;
            margin-right: 10px;
            width:280px;
            height: 40px;
            background-color: pink ;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 6px;
            color: white;
        }
        #load_btn:hover {
            margin-top: 20px;
            width:280px;
            height: 40px;
            background-color: lightcoral;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 6px;
            color: white;
            cursor: pointer;
        }
        #load_btn input{
            width:280px;
            height: auto;
            padding-left: 20px;
        }
        #load_btn input:focus{
            outline: none;
        }
        #save_btn {
            margin-top: 20px;
            margin-left: 10px;
            width: 120px;
            height: 40px;
            background-color: lightgreen;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 6px;
            color: white;
        }
        #save_btn:hover {
            margin-top: 20px;
            width: 120px;
            height: 40px;
            background-color: rgb(36, 235, 36);
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 6px;
            color: white;
            cursor: pointer;
        }
